:root {
  font-family: Rubik, sans-serif;
  font-size: 10px;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.attribution {
  width: auto;
  text-align: end;
}

.finder {
  text-align: center;
  background: url("pattern-bg.93056d32.png") 0 0 / cover;
  padding: 2rem 1.5rem 17.5rem;
  position: relative;
}

@media (min-width: 1024px) {
  .finder {
