  margin-bottom: 1.5rem;
  font-size: 3rem;
  font-weight: 500;
}

.search-bar {
  justify-content: center;
  margin-bottom: 2rem;
  display: flex;
}

.search-bar__input {
  width: 100%;
  max-width: 500px;
  color: #2b2b2b;
  cursor: pointer;
  border: none;
  border-radius: 10px 0 0 10px;
  outline: none;
  padding: 1.5rem 2rem;
  font-size: 1.8rem;
  font-weight: 700;
}

.search-bar__btn {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 0 10px 10px 0;
  outline: none;
  padding: 0 1.5rem;
  position: relative;
}

.search-bar__btn:hover {
  background-color: #4d4d4d;
}

.info {
  width: calc(100% - 5rem);
  z-index: 10000;
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  padding: 2rem;
  display: flex;
  position: absolute;
  left: 2.5rem;
}

@media (min-width: 1024px) {
  .info {
    width: 100%;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    transform: translateY(50%);
  }
}

.info__block {
  flex: 1 0 25%;
  padding: 0 .5rem;
  position: relative;
}

.info__block:not(:last-child) {
  margin-bottom: 1.2rem;
}

.info__block-subtitle {
  color: #969696;
  text-transform: uppercase;
  letter-spacing: .3rem;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  font-weight: 700;
}

.info__block-title {
  color: #2b2b2b;
  font-size: 1.5rem;
  font-weight: 700;
}

#map {
  min-height: calc(100vh - 317px);
}

@media (min-width: 1024px) {
  #map {
    min-height: calc(100vh - 284px);
  }

  .info {
    flex-direction: row;
  }

  .info__block {
    margin-bottom: 0;
  }

  .info__block:after {
    content: "";
    width: 1px;
